.app {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #fff;
}

.container {
  width: 75% !important;
  background: #fff !important;
}


h1,
h5,
p {
  margin: 0;
}

.box {
  width: 20px;
  height: 20px;
  border-radius: 10px;
  cursor: pointer;
  border: 6px solid black;
}

.loading {
  width: 100%;
  height: 90vh;
  background-color: rgba(255, 255, 255, 0.5);
  z-index: 100;
}

.title {
  font-size: 90px;
  color: #fff !important;
  z-index: 100;
}

.tag {
  z-index: 100;
}

img {
  width: 25px;
}

.list {
  box-shadow: 1px 1px 10px #e3e3e3;
  padding: 15px;
  margin: 5px 0;
}

button {
  border: 0;
  font-size: 12px;
  color: rgb(34, 135, 202);
  outline: none;
  background-color: transparent;
}

button:hover,
button:active {
  outline: none;
  color: #0c9;
  transition: all 0.2s linear;
}

input {
  border: 0;
  outline: none;
  border-bottom: 1px solid #e3e3e3;
}

.tab {
  font-size: 12px;
  text-align: center;
  font-weight: bold;
}

.hand {
  cursor: pointer;
}

.bg {
  background: url('../assets/wallpaper.jpg') no-repeat;
  width: 100%;
  height: 100vh;
  background-size: cover;
}

.bg::before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(66, 7, 20, 0.5);

}

.profile {
  border-radius: 50%;
  object-fit: cover;
  width: 25px;
}

@media screen and (max-width: 768px) {

  p,
  .tab {
    font-size: 12px !important;
    font-weight: bold !important;
  }

  .small {
    font-size: 10px !important;
    font-weight: normal !important;
  }

  .container {
    width: 100% !important;
  }
}
